<template>
    <div id="background">
        <!-- // -->
        <van-nav-bar title="" left-arrow @click.native="goBack"></van-nav-bar>
        <!-- // -->
        <div class="decorate"></div>
        <!-- // -->
        <van-cell icon="location-o" is-link id="area" @click="toAddressList">
            <!--  title  -->
            <template slot="title">
                <span class="custom-title"></span>
                <span class="custom-title">&nbsp;</span>
                <span class="custom-title">13000000000</span>
                <div class="custom-title fonts"> 138  7  501 </div>
            </template>
        </van-cell>
        <div id="goods">
        <!-- // -->
        <!-- // -->
        <van-cell-group>
            <van-cell :title="`${expressFee/100}${fullReduceMoney/100}`" />
        </van-cell-group>

        <van-card
            v-for="(item,index) in carts"
            :key="index"
            :num="item.buyNum"
            :price="item.price | formatMoney"
            :desc="item.slaveName"
            :title="item.masterName"
            :thumb="item.imgUrl"
        ></van-card>



        <!-- // -->
        <van-cell-group>
            <van-cell title="" :value="totalMoney" />
        </van-cell-group>
        <van-cell-group v-if="isExpressFee">
            <van-cell title="" :value="`${expressFee/100}`" />
        </van-cell-group>
        </div>
        <div id="cell">
          <coupon @CounponMoney="getCounponMoney"></coupon>
        <van-cell icon="coupon-o" value="0" is-link>
            <!--  title  -->
            <template slot="title">
            <span class="custom-title"></span>
            </template>
        </van-cell>
        <!-- <van-cell icon="comment-o" value="0" is-link>
          
            <template slot="title">
            <span class="custom-title"></span>
            </template>
        </van-cell> -->
        <van-cell icon="gem-o" value>
            <!--  title  -->
            <template slot="title">
            <span class="custom-title"></span>
            
            <span class="custom-title red">0.00</span>
            
            </template>
        </van-cell>
        <van-cell icon="balance-list-o" value="" is-link>
            <!--  title  -->
            <template slot="title">
            <span class="custom-title"></span>
            </template>
        </van-cell>
        </div>
        <div id="bottom">           
            <van-submit-bar :price="totalPay*100" tip button-text="" @submit="onSubmit" />
        </div>
        <PayWay :class="$store.state.payWayFlag?'show':''"></PayWay>
    </div>
</template>

<script>
import PayWay from "@/pages/pay/children/PayWay.vue";
import coupon from '@/pages/coupon/coupon'
export default {
  
    data() {
        return {           
       totalMoney:0,
            carts:[] ,//
            expressFee:0,//
            isExpressFee:true,//
            fullReduceMoney:0,//
            totalPay:0,//          
            counponMoney:0,//          
        };
    },
    components: {
        PayWay,
        coupon
    },
    //
    watch: {
      totalMoney(){    

      if(Number(this.totalMoney)>this.fullReduceMoney/100){
         this.expressFee=0         
          this.isExpressFee=false
          console.log("")
        
        console.log("this.totaPay111111",this.totalPay)
      }else{
        this.totalPay=Number(this.totalMoney)+Number(this.expressFee)/100
        console.log(" this.totalPay",typeof this.totalPay)
        console.log(" this.totalPay22222", this.totalPay)        
      }     
       
      },
      //
      counponMoney(){       
            this.totalPay=Number(this.totalMoney)+Number(this.expressFee)/100-this.counponMoney
        console.log("this.totaPay3333333333",this.totalPay)

      }
    },
    methods: {
      //
      getCounponMoney(value){
        console.log("value",value)
        this.counponMoney=value
      },
      //
      getPreOrderData(){
        let url = "/preOrder/detail"
        let data={
          preOrderId:this.preOrderId
        }
        this.$axios.post(url,data).then(res=>{
          console.log("",res)
          this.carts= res.result.carts
          console.log("",this.carts)
          this.totalMoney = res.result.totalMoney
          this.expressFee = res.result.expressFee
          this.fullReduceMoney = res.result.fullReduceMoney
       
        }).catch(err=>{
          console.log("",err)
        })
      },
     
        goBack() {
            // this.$router.go(-1);
            this.$router.push("/cart/befor")
            console.log("this.SeletCarNum",this.SeletCarNum)
          this.$store.commit("SeletCarNum",[])
          this.$store.commit("totalPrice",0)
            console.log("this.SeletCarNum",this.SeletCarNum)
            // this.$router.push(`/home/detail/${this.productId}`)
        },
        onSubmit() {
            this.$store.commit('payWayFlagChange', !this.$store.state.payWayFlag)
            this.$store.commit('totalPay',this.totalPay)
            this.$store.commit('discount', this.counponMoney)
        },
        toAddressList(){
            this.$router.push("/address/list")
        }
    },
    created() {    
     this.getPreOrderData()    
    },
     computed: {
      totalPrice(){
        let totalPrice=this.$store.state.totalPrice
        return Number(totalPrice)*100
      },
      SeletCarNum(){
          return this.$store.state.SeletCarNum;      
        },
      preOrderId(){
         return this.$store.state.preOrderId; //Id
    },
  
          
    },
};
</script>

<style lang="less" scoped>
#background {
  background-color: #f6f7f8;
}
.van-nav-bar__arrow {
  font-size: 20px;
  line-height: 27px;
}
.van-icon-arrow-left:before {
  color: black;
}
.van-nav-bar {
  height: 44px;
}
.van-nav-bar__title {
  font-size: 17px;
  max-width: 72%;
  color: #2e2f30;
}
.decorate {
  background: #fff url("../../../assets/img/colored-line.png");
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  height: 3px;
}
[class*="van-hairline"]::after {
  border-color: #fafafb;
}
#area {
  margin-bottom: 12px;
  padding: 14px 17px;
  height: 97px;
  .custom-title:nth-child(4) {
    font-size: 13px;
    color: #797d82;
    line-height: 21px;
    width: 280px;
  }
  .van-cell:not(:last-child)::after {
    left: 0;
  }
  .custom-title:nth-child(3) {
    font-size: 15px;
    color: #797d58;
  }
  .custom-title:first-child {
    font-weight: 700;
    font-size: 15px;
  }
  .van-cell__left-icon {
    margin-top: 12px;
    font-size: 27px;
    color: rgb(189, 193, 197);
  }
  .van-cell__right-icon {
    margin-top: 24px;
  }
  .van-cell {
    margin-bottom: 12px;
  }
}
#goods {
  margin-bottom: 12px;
  .van-cell__title {
    color: #797d82;
    font-size: 13px;
  }
  .van-cell__value {
    color: #c03131;
    font-size: 13px;
  }
  .van-card__bottom {
    padding-top: 28px;
    font-size: 16px;
  }
  .van-card {
    background-color: #fff;
    padding: 14px 16px;
  }
  .van-card__title {
    // font-weight: 700;
    font-size: 15px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    height: 22px;
    line-height: 22px;
  }

  .van-card__desc {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 13px;
    color: #797d82;
  }
  .van-card__price {
    color: #191a1b;
    font-weight: 500;
    font-size: 12px;
  }
  .van-card__num {
    font-size: 12px;
    color: #bdc0c5;
  }
  .van-image {
    background-color: #f8f8f8;
  }
  .van-image__img {
    height: 85px;
    width: 85px;
  }
  .van-card:not(:first-child)::after {
    position: absolute;
    //   box-sizing: border-box;
    content: " ";
    right: 0;
    bottom: 0;
    left: 16px;
    border-bottom: 1px solid #f7f8fa;
  }
  .van-card:not(:first-child) {
    margin-top: 0px;
  }
  .van-cell-group {
    padding: 10px 15px 0px;
  }
  [class*="van-hairline"]::after {
    position: absolute;
    box-sizing: border-box;
    content: " ";
    pointer-events: none;
    top: -50%;
    right: -50%;
    bottom: -50%;
    left: -50%;
    border: 0 solid #ebedf0;
    // -webkit-transform: scale(0.5);
    // transform: scale(0.5);
  }
  .van-cell {
    padding: 0;
    color: #797d82;
  }
}
#cell {
  height: 200px;
  // margin-bottom: 12px;
  .van-cell--clickable {
    height: 50px;
    line-height: 30px;
  }
  .van-cell {
    height: 50px;
    line-height: 30px;
  }
  .van-cell__title {
    font-size: 15px;
    color: #191a1b;
  }
  .red {
    font-size: 13px;
    color: #c03131;
  }
  .van-cell__value {
    font-size: 14px;
    color: #191a1b;
  }
  .van-icon {
    margin-top: 4px;
  }
  .van-icon-coupon-o:before {
    color: #ee0a24;
    font-size: 20px;
  }
  .van-icon-comment-o:before {
    color: #f00a1d;
    font-size: 20px;
  }
  .van-icon-gem-o:before {
    color: orangered;
    font-size: 20px;
  }
  .van-icon-balance-list-o:before {
    color: salmon;
    font-size: 20px;
  }
}
#bottom {
  height: 50px;
  .van-submit-bar__text {
    font-size: 15px;
    color: #191a1b;
  }
  .van-submit-bar__price {
    font-size: 16px;
    color: #c03131;
  }
  .van-button--danger {
    color: #fff;
    background-color: #c03131;
    border: 1px solid #c03131;
  }
}
.show {
    display: none;
}
</style>